import React, { Component } from 'react'
import {Card, Button, Steps, Popover, Timeline,message} from 'antd';
import moment from 'moment';
import YesImg from './yes.png';
import { postAction,getAction } from '@/axios';

const auditStatusList = [
    {color:"#52C41A",text:"通过"},
    {color:"red",text:"退回"},
    {color:"#52C41A",text:"审核中"},
    {color:"orange",text:"待审批"}
]
function findStatus(text){
    let arr = auditStatusList.find(item => item.text===text);
    if(arr){
        return arr
    }
    return false;
}
//步骤条
const stepComponent = (stepsData = {current:0,setps:[]}) => {
    if(!stepsData.setps){
        return <div style={{textAlign:"center"}}>暂无数据</div>
    }
    return (
        <Steps current={Number(stepsData.current)} progressDot={true} style={{marginTop:"50px"}}>
            {
                stepsData.setps.map((item,index) => (
                    <Steps.Step key={index} title={item.title} description={
                        <Popover
                            content={
                                <Timeline>
                                    {
                                        item.toolTips?item.toolTips.map((el,index) => (
                                            <Timeline.Item key={index}>
                                                <p style={{color:"rgba(0,0,0,0.65)"}}>
                                                    {el.job}
                                                    <span style={{color:"#000000",padding:"0 5px"}}>{el.name}</span>
                                                    {
                                                        findStatus(el.status)?<span style={{color:findStatus(el.status).color}}>{findStatus(el.status).text}</span>:"状态未知"
                                                    }
                                                </p>
                                                {
                                                    (el.status==='通过' || el.status==='退回')?
                                                    <>
                                                        <p style={{color:"rgba(0,0,0,0.65)"}}>审批意见：<span>{el.comments}</span></p>
                                                        <p style={{color:"rgba(0,0,0,0.65)"}}>{moment.unix(el.time/1000).format("YYYY/MM/DD HH:mm:ss")}</p>
                                                    </>:null
                                                } 
                                            </Timeline.Item>
                                        )):null
                                    }
                                </Timeline>
                            }
                        >
                            <span style={{cursor:"pointer",color:"#3F54A2"}}>{item.detail}{item.detail?item.approveType===1?'(或签)':'(会签)':''}</span>
                        </Popover>
                    } />
                ))
            }
        </Steps>
    )
}

export default class Step5 extends Component {
    constructor(props){
        super(props);
        this.state = {
            name:"",
            planTaskId:"",//计划id
            startTime:null,
            endTime:null,
            progress:{current:0,setps:[]},
        }
    }
    componentDidMount(){
        let fillPlanInfo = JSON.parse(window.localStorage.getItem("fillPlanInfo"));
        this.setState({
            planTaskId:fillPlanInfo.planTaskId,
        }, this.getInfoRequest)
    }
    //获取流程信息
    getInfoRequest = () => {
        postAction("/ybt/planTask/getPlanTaskById",{planTaskId:this.state.planTaskId}).then(res => {
            console.log(res);
            if(res.success){
                this.setState({
                    name:res.obj.planTaskName,
                    startTime:res.obj.taskPlanStartTime,
                    endTime:res.obj.taskPlanEndTime
                });
                this.getProgressRequest(this.state.planTaskId,res.obj.processId)
            }
        }); 
    }
    //获取流程进度
    getProgressRequest =(planTaskId,processId) => {
        getAction(`/ybt/planTask/queryPlanTaskProcessList?planTaskId=${planTaskId}&processId=${processId}`).then(res => {
            if(res.success){
                this.setState({
                    progress:res.obj
                })
            }else{
                message.error(res.obj)
            }
        })
    }
    toFillPlan = () => {
        this.props.history.push('/fillPlan')
    }

    render() {
        return (
            <div style={{padding: "20px"}}>
                <Card title={null} style={{ width: "100%", boxShadow:"0px 0px 2px 2px #f1f1f1" }}>
                    <p style={{textAlign:"center"}}>
                        <img src={YesImg} alt='' style={{width:"80px"}} />
                    </p>
                    <p style={{textAlign:"center", fontWeight: "bold", fontSize:"24px"}}>
                        创建完成
                    </p>
                    <p style={{textAlign:"center", color:"rgba(0,0,0,0.45)"}}>
                        已完成填报计划创建，经过计划审核人审核后，下发到填报人。
                    </p>

                    <div style={{width:"826px", height:"280px",fontSize:"16px",background:"rgba(249,250,251,1)", margin: "0px auto", padding:"20px"}}>

                        <p style={{marginLeft:"40px"}}>
                            <span>计划名称：</span>
                            <span>{this.state.name}</span>
                        </p>

                        <p style={{marginLeft:"40px"}}>
                            <span>计划时间：</span>
                            <span>{moment.unix(this.state.startTime/1000).format("YYYY/MM/DD HH:ss:mm")} - {moment.unix(this.state.endTime/1000).format("YYYY/MM/DD HH:ss:mm")}</span>
                        </p>

                        {/* <Steps progressDot current={1} style={{marginTop: "30px"}}>
                            <Step title="创建计划" description={
                                (
                                    <span>
                                        <span>张三</span>
                                        <br />
                                        <span>2018/09/13 12:00:00</span>
                                    </span>
                                )
                            } />
                            <Step title="表单填报" description="张三等12人" />
                            <Step title="填报审核" description="张三等12人" />
                            <Step title="计划完成" description="" />
                        </Steps> */}
                        {stepComponent(this.state.progress)}
                    </div>

                    <p style={{textAlign:"center"}}>
                        <Button type="primary" style={{width: "100px"}} onClick={this.toFillPlan}>完成</Button>
                    </p>

                </Card>
            </div>
        )
    }
}
